@import "./common.scss";
main {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  // background-color: #f4f4f9;

  margin: 0 auto;
  width: 100vw;
  max-width: 1200px;
  aside {
    box-sizing: border-box;
    width: 30%;
    padding: 20px 15px 10px 15px;
    top: 7vh;
    margin: 2px;
    max-height: 80%;
    min-height: 80vh;
    overflow-x: hidden;
    overflow-y: auto;
    text-overflow: ellipsis;
    h2 {
      margin: 0;
      padding: 0;
      font-size: 20px;
      .fas.fa-sort-down {
        display: none;
      }
    }
    ul {
      display: flex;
      flex-direction: column;
      list-style: none;
      padding: 0;
      width: 100%;
      li {
        margin: 0;
        margin-bottom: 15px;
        padding-bottom: 5px;
        width: 95%;
        border-bottom: 1px solid rgba($color: $mondrianB, $alpha: 0.2);
        a {
          text-decoration: none;
          color: $mondrianB;
        }
      }
    }
  }
  article {
    box-sizing: border-box;
    padding: 15px 30px 15px 30px;
    width: 78%;
    h3.sub {
      color: #132E55;
      & :nth-child(2) {
        margin-left: 20%;
      }
    }
    img {
      max-width: 90%;
      margin: 15px 0px 15px 5px;
    }
    #content {
      line-height: 1.5rem;
      margin-top: 30px;
      margin-bottom: 100px;
      a {
        color: $mondrianB;
        text-decoration: none;
      }
    }
    #comment {
      button {
        background-color: $mondrianY;
        border-radius: 15px;
        padding: 4px 8px 4px 8px;
        font-weight: bold;
        &:hover {
          cursor: pointer;
        }
      }
      .commentMain {
        display: none;
        &.active {
          display: inline;
        }
      }
    }
  }
  .margin {
    width: 3%;
  }
}
@media (max-width: 700px) {
  main {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 100vw;
    aside {
      box-sizing: border-box;
      width: 90vw;
      margin-left: 4vw;
      border-radius: 20px;
      background-color: #dfdfdf;
      min-height: 60px;
      max-height: 500px;
      ul {
        display: none;
        flex-direction: column;
        &.active {
          display: flex;
        }
      }
      h2 {
        .fas.fa-sort-down {
          display: inline;
        }
        &:hover {
          cursor: pointer;
          text-decoration: underline;
        }
      }
    }
    article {
      padding: 25px 0px 10px 0px;
      margin-left: 5%;
      width: 90%;

      img {
        max-width: 90%;
      }
    }
    .margin {
      width: 3%;
    }
  }
}
