@import './common.scss';

.nav-items .nav-a.active {
  color: black;
  border-bottom: 3px solid $mondrianY;
}

.nav-items .nav-b.active {
  color: black;
  border-bottom: 3px solid $mondrianB;
}

.nav-items .nav-c.active {
  color: black;
  border-bottom: 3px solid $mondrianR;
}
.nav-items .nav-d.active {
  color: black;
  border-bottom: 3px solid #57396A;
}

html,
body {
  padding: 0px;
  margin: 0px;
  @include font-myung;
  &::-webkit-scrollbar {
    width: 11px;
  }
  &::-webkit-scrollbar-thumb {
    background-color: $mondrianB;
    border-radius: 10px;
    background-clip: padding-box;
    border: 1px solid transparent;
  }
  &::-webkit-scrollbar-track {
    background-color: #ddd;
    border-radius: 10px;
    box-shadow: inset 0px 0px 5px white;
  }
}
nav {
  box-sizing: border-box;
  position: fixed;
  top: 0px;
  width: 130vw;
  height: 7vh;
  left: -15vw;
  @include flexrow;
  @include font-hyeon;
  font-size: 16px;
  z-index: 100;
  background-color: rgba($color: white, $alpha: 0.75);
  .nav-brand {
    padding-top: 6px;
    // margin: 0px 2vw 0px 2vw;
    font-size: 22px;
    color: #312349;
    text-decoration: none;
    text-shadow: -1px 0 #eee, 0 1px #eee, 1px 0 #eee, 0 -1px #eee;
  }
  .nav-items a {
    margin: 0px 2vw 0px 2vw;
    color: #888;
    text-decoration: none;
  }
}
@media (max-width: 650px) {
  nav {
    .nav-items {
      @include flexrow();
      position: fixed;
      z-index: -99;
      top: 7vh;
      left: 100vw;
      width: 100%;
      background-color: rgba($color: white, $alpha: 0.7);
      padding: 10px;
      animation-name: slideout;
      animation-duration: 0.7s;
    }
    .hamburg {
      display: block;
      color: #666;
      -webkit-animation-duration: 0.7s;
      -webkit-animation-name: turn2;
      animation-duration: 0.7s;
      animation-name: turn2;
      &:hover {
        cursor: pointer;
      }
      &.active {
        -webkit-transform: rotate(45deg);
        transform: rotate(45deg);
        -webkit-animation-duration: 0.7s;
        -webkit-animation-name: turn1;
        animation-duration: 0.7s;
        animation-name: turn1;
      }
    }
    .nav-items.active {
      left: 0vw;
      animation-name: slidein;
      animation-duration: 0.7s;
    }
  }
}
@media (min-width: 650px) {
  nav {
    padding: 2vh 15vw 0px 15vw;
    .nav-items {
      @include flexrow;
      color: black;
    }
    .hamburg {
      display: none;
    }
  }
}
.content {
  position: relative;
  padding-top: 7vh;
}
@keyframes turn1 {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(45deg);
  }
}

@keyframes turn2 {
  from {
    transform: rotate(45deg);
  }
  to {
    transform: rotate(0deg);
  }
}
@keyframes slidein {
  from {
    left: 100vw;
  }
  to {
    left: 0vw;
  }
}
@keyframes slideout {
  from {
    left: 0vw;
  }
  to {
    left: 100vw;
  }
}
footer{
  position: absolute;
  box-sizing: border-box;
  bottom: 0;
  left: 0;
  margin-top: 12px;
  margin-bottom: 0px;
  width: 100vw;
  padding: 5px;
  border-top: inset 1px #ddd;
  background-color: rgba($color: $mondrianB, $alpha: 0.1) ;
  border-radius: -10px;
  text-align: center;
  a{
    text-decoration: none;
    color: #555;
  }
}