html,body{margin:0;padding:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}html div.adsbox,body div.adsbox{position:fixed;top:-100px}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}#root{position:relative;padding-top:7vh;min-height:93vh;padding-bottom:50px}

body,#root{overflow-x:hidden}.main1{box-sizing:border-box;margin-left:8vw;height:73vh;position:relative;z-index:3}.main1 h1{font-family:"Sunflower", sans-serif;font-weight:300;margin-bottom:0px;margin-top:20vh;overflow-x:hidden;text-shadow:0px -1px 0px #fff}.main1 h2{margin-top:0px;margin-bottom:10px;font-family:"Sunflower", sans-serif;font-weight:300}.main1 h6{margin-top:0px;font-family:"Sunflower", sans-serif;font-weight:300}.main1 h1{font-size:5rem}.main1 h2{font-size:1.8rem}.main1 h6{font-size:1rem;padding-left:15px}.main2{position:relative;height:100vh;width:100vw;overflow-x:hidden}.main2 .red{background-color:#dd0100;position:fixed;z-index:1;top:7vh;right:0px;width:63vw;height:53vh}.main2 .blackH1{background-color:#000;position:fixed;z-index:2;top:60vh;left:0px;width:100%;height:5vh}.main2 .blackV1{background-color:#000;position:fixed;z-index:2;top:7vh;left:33vw;width:5vw;height:93vh}.main2 .blue{background-color:#225095;position:fixed;z-index:1;top:64vh;left:0px;width:33vw;height:39vh}.main2 .blackH2{background-color:#000;position:fixed;z-index:2;top:27vh;left:0px;width:33vw;height:5vh}.main2 .blackV2{background-color:#000;position:fixed;z-index:2;top:64vh;left:66vw;width:5vw;height:36vh}.main2 .blackH3{background-color:#000;position:fixed;z-index:2;top:79vh;right:0px;width:30vw;height:5vh}.main2 .yellow{background-color:#fac901;position:fixed;z-index:1;top:64vh;right:-10vw;width:40vw;height:15vh}.main2 .m2hidden{box-sizing:border-box;width:100%;height:100%;padding:40px;font-size:2rem}.main2 .m2hidden a{color:white;text-decoration:none}.main2 .m2hidden a i{font-size:1.5vw}.main3{height:200px;z-index:-99;pointer-events:none}

.nav-items .nav-a.active{color:black;border-bottom:3px solid #fac901}.nav-items .nav-b.active{color:black;border-bottom:3px solid #225095}.nav-items .nav-c.active{color:black;border-bottom:3px solid #dd0100}.nav-items .nav-d.active{color:black;border-bottom:3px solid #57396A}html,body{padding:0px;margin:0px;font-family:"Nanum Myeongjo", serif;font-weight:400}html::-webkit-scrollbar,body::-webkit-scrollbar{width:11px}html::-webkit-scrollbar-thumb,body::-webkit-scrollbar-thumb{background-color:#225095;border-radius:10px;background-clip:padding-box;border:1px solid transparent}html::-webkit-scrollbar-track,body::-webkit-scrollbar-track{background-color:#ddd;border-radius:10px;box-shadow:inset 0px 0px 5px white}nav{box-sizing:border-box;position:fixed;top:0px;width:130vw;height:7vh;left:-15vw;display:flex;flex-direction:row;justify-content:space-around;align-items:center;font-family:"Do Hyeon", sans-serif;font-weight:400;font-size:16px;z-index:100;background-color:rgba(255,255,255,0.75)}nav .nav-brand{padding-top:6px;font-size:22px;color:#312349;text-decoration:none;text-shadow:-1px 0 #eee, 0 1px #eee, 1px 0 #eee, 0 -1px #eee}nav .nav-items a{margin:0px 2vw 0px 2vw;color:#888;text-decoration:none}@media (max-width: 650px){nav .nav-items{display:flex;flex-direction:row;justify-content:space-around;align-items:center;position:fixed;z-index:-99;top:7vh;left:100vw;width:100%;background-color:rgba(255,255,255,0.7);padding:10px;animation-name:slideout;animation-duration:0.7s}nav .hamburg{display:block;color:#666;-webkit-animation-duration:0.7s;-webkit-animation-name:turn2;animation-duration:0.7s;animation-name:turn2}nav .hamburg:hover{cursor:pointer}nav .hamburg.active{transform:rotate(45deg);-webkit-animation-duration:0.7s;-webkit-animation-name:turn1;animation-duration:0.7s;animation-name:turn1}nav .nav-items.active{left:0vw;animation-name:slidein;animation-duration:0.7s}}@media (min-width: 650px){nav{padding:2vh 15vw 0px 15vw}nav .nav-items{display:flex;flex-direction:row;justify-content:space-around;align-items:center;color:black}nav .hamburg{display:none}}.content{position:relative;padding-top:7vh}@keyframes turn1{from{transform:rotate(0deg)}to{transform:rotate(45deg)}}@keyframes turn2{from{transform:rotate(45deg)}to{transform:rotate(0deg)}}@keyframes slidein{from{left:100vw}to{left:0vw}}@keyframes slideout{from{left:0vw}to{left:100vw}}footer{position:absolute;box-sizing:border-box;bottom:0;left:0;margin-top:12px;margin-bottom:0px;width:100vw;padding:5px;border-top:inset 1px #ddd;background-color:rgba(34,80,149,0.1);border-radius:-10px;text-align:center}footer a{text-decoration:none;color:#555}

main{display:flex;flex-direction:row;justify-content:space-around;margin:0 auto;width:100vw;max-width:1200px}main aside{box-sizing:border-box;width:30%;padding:20px 15px 10px 15px;top:7vh;margin:2px;max-height:80%;min-height:80vh;overflow-x:hidden;overflow-y:auto;text-overflow:ellipsis}main aside h2{margin:0;padding:0;font-size:20px}main aside h2 .fas.fa-sort-down{display:none}main aside ul{display:flex;flex-direction:column;list-style:none;padding:0;width:100%}main aside ul li{margin:0;margin-bottom:15px;padding-bottom:5px;width:95%;border-bottom:1px solid rgba(34,80,149,0.2)}main aside ul li a{text-decoration:none;color:#225095}main article{box-sizing:border-box;padding:15px 30px 15px 30px;width:78%}main article h3.sub{color:#132E55}main article h3.sub :nth-child(2){margin-left:20%}main article img{max-width:90%;margin:15px 0px 15px 5px}main article #content{line-height:1.5rem;margin-top:30px;margin-bottom:100px}main article #content a{color:#225095;text-decoration:none}main article #comment button{background-color:#fac901;border-radius:15px;padding:4px 8px 4px 8px;font-weight:bold}main article #comment button:hover{cursor:pointer}main article #comment .commentMain{display:none}main article #comment .commentMain.active{display:inline}main .margin{width:3%}@media (max-width: 700px){main{display:flex;flex-direction:column;justify-content:flex-start;width:100vw}main aside{box-sizing:border-box;width:90vw;margin-left:4vw;border-radius:20px;background-color:#dfdfdf;min-height:60px;max-height:500px}main aside ul{display:none;flex-direction:column}main aside ul.active{display:flex}main aside h2 .fas.fa-sort-down{display:inline}main aside h2:hover{cursor:pointer;text-decoration:underline}main article{padding:25px 0px 10px 0px;margin-left:5%;width:90%}main article img{max-width:90%}main .margin{width:3%}}

