@import './common.scss';

body,
#root {
  overflow-x: hidden;
}
.main1 {
  box-sizing: border-box;
  margin-left: 8vw;
  height: 73vh;
  position: relative;
  z-index: 3;
  h1 {
    @include font-sunflower();
    margin-bottom: 0px;
    margin-top: 20vh;
    overflow-x: hidden;
    text-shadow: 0px -1px 0px #fff;
  }
  h2 {
    margin-top: 0px;
    margin-bottom: 10px;
    @include font-sunflower();
  }

  h6 {
    margin-top: 0px;
    @include font-sunflower();
  }

  h1 {
    font-size: 5rem;
  }
  h2 {
    font-size: 1.8rem;
  }
  h6 {
    font-size: 1rem;
    padding-left: 15px;
  }
}
.main2 {
  position: relative;
  height: 100vh;
  width: 100vw;
  overflow-x: hidden;
  .red {
    background-color: $mondrianR;
    position: fixed;
    z-index: 1;

    top: 7vh;
    right: 0px;
    width: 63vw;
    height: 53vh;
  }

  .blackH1 {
    background-color: #000;
    position: fixed;
    z-index: 2;

    top: 60vh;
    left: 0px;
    width: 100%;
    height: 5vh;
  }
  .blackV1 {
    background-color: #000;
    position: fixed;
    z-index: 2;

    top: 7vh;
    left: 33vw;
    width: 5vw;
    height: 93vh;
  }
  .blue {
    background-color: $mondrianB;
    position: fixed;
    z-index: 1;

    top: 64vh;
    left: 0px;
    width: 33vw;
    height: 39vh;
  }
  .blackH2 {
    background-color: #000;
    position: fixed;
    z-index: 2;

    top: 27vh;
    left: 0px;
    width: 33vw;
    height: 5vh;
  }

  .blackV2 {
    background-color: #000;
    position: fixed;
    z-index: 2;

    top: 64vh;
    left: 66vw;
    width: 5vw;
    height: 36vh;
  }
  .blackH3 {
    background-color: #000;
    position: fixed;
    z-index: 2;

    top: 79vh;
    right: 0px;
    width: 30vw;
    height: 5vh;
  }
  .yellow {
    background-color: $mondrianY;
    position: fixed;
    z-index: 1;

    top: 64vh;
    right: -10vw;
    width: 40vw;
    height: 15vh;
  }
  .m2hidden {
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    padding: 40px;
    font-size: 2rem;
    a {
      color: white;
      text-decoration: none;
      i {
        font-size: 1.5vw;
      }
    }
  }
}
.main3 {
  height: 200px;
  z-index: -99;
  pointer-events: none;
}
