$mondrianY: #fac901;
$mondrianB: #225095;
$mondrianR: #dd0100;

@import url("https://fonts.googleapis.com/css2?family=Do+Hyeon&family=Nanum+Myeongjo&family=Sunflower:wght@300&family=Gaegu:wght@300&display=swap");
@mixin font-hyeon {
  font-family: "Do Hyeon", sans-serif;
  font-weight: 400;
}
@mixin font-myung {
  font-family: "Nanum Myeongjo", serif;
  font-weight: 400;
}
@mixin font-gaegu {
  font-family: "Gaegu", cursive;
}
@mixin font-sunflower {
  font-family: "Sunflower", sans-serif;
  font-weight: 300;
  // font-style: li;
}
@mixin flexrow($jty-con: space-around) {
  display: flex;
  flex-direction: row;
  justify-content: $jty-con;
  align-items: center;
}
@mixin flexcol($jty-con: space-around) {
  display: flex;
  flex-direction: column;
  justify-content: $jty-con;
  align-items: center;
}